html, body, #app, #app>div, #root {
  height: 100%
}

body {
    background: #f9f5d7;
    font-family: 'Terminus', monospace;
}

.window-heading {
    text-align: center;
}

#root {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.window-shadowed {
	width: 98%;	
	height: 75%;
	position: absolute;
	background-color: #d5c4a1;
	border-left: .6ch solid #d5c4a1;
	border-right: .6ch solid #d5c4a1;
	border-top: 1ch solid #d5c4a1;
	border-bottom: 1ch solid #d5c4a1;
	box-sizing: border-box;
}

.window-inner {
	width: 100%;
	height: 100%;
	margin:auto;
	border: 2px solid #7c6f64;
	box-sizing:border-box;
}

.window-shadowed:after {
	content: "";
	width: 100%;
	height: 100%;
	position: absolute;
	top: 30px;	
	left: 20px;	
	z-index: -2;
	background-image: url('https://i.ibb.co/vsKjWzM/shadowchar.png');
	background-repeat: repeat;
}